
html{
  background-color: #151E25;
  overflow-y: hidden;
  overflow-x: hidden;
  font-size: 62.5% !important;
  line-height: 62.5% !important;
}

body{
  margin: 0;
  padding: 0;
  overflow-y: hidden;
  overflow-x: hidden;
}

h1, h3, h4{
  margin: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  padding: 0;
}

ul{
  margin: 0;
  padding: 0;
}

html, body, #root {
  height: 100%;
}