.App{
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  /* width: 100vh; */
  height: 100vw; 
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
}

.wrapper{
  display: flex; 
  flex-direction: row;
  transform: rotate(90deg) translateY(-100vh);
  transform-origin: top left;
  width: 480rem;
}

.section_dimensions{
  border-right: 1px solid #8CA5A1;
  /* width: 100vh; */
  /* height: 100vw; */
}




@media screen and  (max-width: 800px) {
      
    .App{
      transform: rotate(360deg) translateX(0vh);
      height: 100vh !important;
      width: 100vw !important; 
      /* overflow-y: unset;
      overflow-x: scroll;*/
      transform-origin: top bottom; 
    }

    .wrapper{
      flex-direction: column;
      transform: rotate(360deg) translateY(0vh);
      height: auto !important;
      width: 100vw !important;
      /* overflow-y: unset;
      overflow-x: scroll; */
      transform-origin: none !important;
    }
}
